:root {
    --font-dark: rgb(19, 15, 83);
    --white-1: rgb(246, 246, 246);
    --white-2: rgb(242, 242, 242);
    --accent-1: rgb(65, 111, 216);
    /*--accent-1: rgb(0, 82, 165);// <- podoba mi sie ten */
    --accent-1-light: rgb(122, 158, 239);
    --accent-1-faded: rgb(65, 111, 216);
    --accent-2: rgb(238, 204, 35);
    --accent-2-light: rgb(241, 218, 105);
    --accent-2-faded: rgb(238, 204, 35);
    --accent-3: rgba(112, 112, 112, 0.78)
}

html {
    min-height: 100vh;
    width: 100vw;
}
body {
    font-family: "Nunito", sans-serif;
    font-size: 17px;
    min-width: 360px;
    min-height: 100%;
    width: 100vw;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    background-color: rgb(246, 246, 246);
    background-color: var(--white-1);
    color: rgb(19, 15, 83);
    color: var(--font-dark);
}

div.content{
    width: 100%;
    min-height: 80vh;
}

ul {
    font-size: 0.8em;
    list-style: none;
    margin: 0;
    padding: 0;
}

button {
    height: auto;
    border-radius: 50px;
    padding: 8px 30px;
    font-family: "Rowdies", cursive;
    font-size: 0.8em;
    text-transform: uppercase;
    white-space: nowrap;
    border: none;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

button.button-accent-1 {
    background-color: rgb(65, 111, 216);
    background-color: var(--accent-1);
    color: rgb(246, 246, 246);
    color: var(--white-1);
}

button.button-accent-1:hover {
    background-color: rgb(122, 158, 239);
    background-color: var(--accent-1-light);
}

button.button-accent-2 {
    background-color: rgb(238, 204, 35);
    background-color: var(--accent-2);
    color: rgb(19, 15, 83);
    color: var(--font-dark);
}

button.button-accent-2:hover {
    background-color: rgb(241, 218, 105);
    background-color: var(--accent-2-light);
}

div.post h2{
    font-family: "Nunito", sans-serif;
}

h1, h2, h3 {
    font-family: "Rowdies", cursive;
    text-transform: uppercase;
}

h1 {
    font-size: 1.2em;
}

h2 {
    font-size: 1.1em;
}

h3 {
    font-size: 1em;
}

p {
    font-size: 0.9em;
}

small {
    font-size: 0.7em;
    font-weight: 300;
}

a:link, a:visited {
    color: rgb(65, 111, 216);
    color: var(--accent-1);
}

a:hover {
    color: rgb(122, 158, 239);
    color: var(--accent-1-light);
}

a:active {
    font-weight: bold;
    color: rgb(65, 111, 216);
    color: var(--accent-1);
}

div.attachment{
    /*max-height: 20px;*/
    margin: 10px 0;
    padding: 5px 10px;
    background-color: rgb(241, 218, 105);
    background-color: var(--accent-2-light);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

div.attachment p{
    font-size: 0.8em;
    margin: 0;
    margin-right: 10px;
    display: inline-block;
}

div.attachment a div.attachment-download{
    width: 10px;
    height: 10px;
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
}

div.attachment a div.attachment-download:hover{
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
}


.header {
    background-color: var(--accent-1);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.header-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: space-between;
}

.header #mobile-menu, #short-showcase, #mobile-spacer {
    display: none;
}

/* Showcase */

.header #showcase {
    display: flex;
    flex-direction: row;
}

.header #showcase svg{
    margin: 0.5em;
    width: 100px;
    height: auto;
    align-self: center;
}


.header #showcase ul#school-name{
    color: var(--white-1);
    font-family: Rowdies, cursive;
    font-weight: lighter;
    font-size: medium;
    align-self: center;
}

/* Menu container = contact ribbon + menu */

#menu-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /*flex: 1;*/
}

/* Contact ribbon */

.header #ribbon {
    padding: 0.5em 1em;
    border-bottom-left-radius: 10em;
    border-bottom-right-radius: 10em;
    background-color: var(--accent-2);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    font-size: smaller;
}


.header #ribbon #ribbon-contact, #ribbon-links {
    display: flex;
    flex-direction: row;
    align-items: center;
    -webkit-margin-start: 0.5em;
            margin-inline-start: 0.5em;
    -webkit-margin-end: 0.5em;
            margin-inline-end: 0.5em;
}

.ribbon-data {
    display: flex;
    flex-direction: row;
    align-items: center;
    -webkit-margin-end: 0.5em;
            margin-inline-end: 0.5em;
}

#ribbon a {
    height: 1.5em;
    margin: 0;
}

#ribbon svg {
    height: 1.5em;
    margin: 0;
}

#ribbon .image-button path{
    fill: var(--font-dark);
}

#ribbon .image-button path:hover {
    fill: var(--accent-1);
}

/* smaller pc */

@media (max-width: 1200px) {
    .header #ribbon {
        border-bottom-right-radius: 0;
    }
}

/*@media (max-width: 1200px) {*/

/*    #ribbon-links {*/
/*        margin-inline-start: 2em;*/
/*    }*/
/*}*/

/* tablet */

@media (max-width: 1065px) {

    .header {
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        left: 0;
        right: 0;

        overflow-x: hidden;
    }

    .header #showcase svg {
        width: 70px;
    }

    .header #showcase {
        font-size: small;
    }

    .header #mobile-menu {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
    }

    .header #mobile-menu>* {
        height: 1.7em;
        width: 1.7em;
        margin: 1em;
        stroke-width: 7;
    }

    .header #mobile-menu svg path {
        stroke-width: 7;
    }

    .header #ribbon {
        display: none;
    }
}

/* phone */

@media (max-width: 600px) {

    .header #showcase svg {
        display: none;
    }

    .header #showcase {
        font-size: x-small;
        margin: 0.5em;
    }

    .header #mobile-menu>* {
        height: 1.5em;
        width: 1.5em;
        margin: 0.5em;
        stroke-width: 7;
        /*filter: drop-shadow(0 4px 4px var(--accent-3));*/
    }

    .header #mobile-menu>*>div {
        width: 1.5em;
    }

    .header #mobile-menu svg path {
        stroke-width: 7;
    }

    .header #mobile-menu{
        justify-content: flex-end;
        margin-right: 0.5em;
    }

}
