#side-menu {
    position: absolute;
    right: 0;
    height: 100vh;
    width: 60%;
    display: flex;
    flex-direction: column;
    background-color: var(--accent-2-light);
    z-index: 15;
    font-family: Rowdies, cursive;
    text-transform: uppercase;
}

#side-menu * {
    text-decoration: none;
}

#side-menu a {
    color: var(--accent-1);
}

#side-menu li {
    list-style: none;
    font-size: larger;
}

#side-menu>div {
    display: flex;
    align-items: center;
    padding: 1em;
}
div.content{
    width: 100%;
    min-height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

div.content div.photo {
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    width: 100%;
    margin-bottom: 20px;
}

div.content div.photo-1 {
    background-image: url(/static/media/photo_home_1.e947bcbf.jpg);
    height: 400px;
}

div.content div.photo-2 {
    background-image: url(/static/media/photo_home_2.93643b0e.jpg);
    height: 200px;
}

div.content div#shortcut {
    width: 90%;
    max-width: 1200px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 5px 0 20px 0;
}

div.content div#shortcut a{
    min-width: 160px;
    margin: 10px 10px;
    padding: 5px 20px;
    flex: 1 1;
    background-color: var(--accent-1);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 10px;
    color: var(--white-1);
    text-decoration: none;
}

div.content div#shortcut a:hover {
    background-color: var(--accent-1-light);
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2), 0 8px 22px 0 rgba(0, 0, 0, 0.19);
    color: var(--white-1);
}

a:link, a:visited, a:active {
    color: var(--white-1);
}

div.content div#shortcut a div h3 {
    text-align: center;
    white-space: nowrap;
}
div.posts-container{
    max-width: 1200px;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 5% 30px 5%;
}

div.post{
    display: flex;
    flex-wrap: wrap;
    width: 70%;
    margin-bottom: 30px;
    padding: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

div.post.post-left {
    flex-direction: row-reverse;
}

div.post:last-child{
    margin-bottom: 0;
}

div.post>div:first-child{
    flex: 1 1;
}

div.post>div:last-child{
    flex: 3 1;
    min-width: 200px;
}

div.post>div {
    margin: 10px;
    display: flex;
    flex-direction: column;
}

div.post>div>img {
    width: 100%;
    min-width: 150px;
    height: 150px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    object-fit: cover;
}

div.post>div>div>div.post-attachment:first-of-type {
    margin-top: 20px;
}

div.post div button{
    align-self: flex-end;
    justify-self: flex-end;
}

div.post.post-left div button {
    align-self: flex-start;
}

div.post div:last-child>p{
    flex: 2 1;
}

div.post div:last-child>p>p{
    margin: 0;
    display: inline;
}

div.post div:last-child h2{
    margin: 0;
    margin-bottom: 5px;
}

@media only screen and (min-width: 1600px) {
    div.post>div:last-child{
        flex: 4 1;
    }
}

@media only screen and (min-width: 1300px) {
    div.post>div>img {
        width: 100%;
        min-width: 200px;
    }
}

@media only screen and (max-width: 960px) {
    div.post{
        width: 80%;
    }
}

@media only screen and (max-width: 750px) {
    div.post{
        width: 90%;
    }
}

@media only screen and (max-width: 666px) {
    div.post>div:first-child>div{
        flex: 1 1;
    }

    div.post>div>img {
        width: 100%;
        min-width: 200px;
    }

    div.post>div:first-child>div{
        min-width: 70px;
    }

}

@media only screen and (max-width: 543px) {
    div.post.post-left div button {
        align-self: flex-end;
    }
}

@media only screen and (max-width: 370px) {
    div.post>div:last-child{
        min-width: 200px;
    }
}
.footer {
    margin-top: 50px;
    bottom: 0;
    width: 100%;
    box-shadow: 0 -4px 8px 0 rgba(0, 0, 0, 0.2);

}

.footer h3 {
    margin-bottom: 5px;
}

.footer p {
    font-size: 0.8em;
}

.footer #top {
    background-color: var(--accent-1-light);
    color: var(--white-1);
    padding: 20px;
}

.footer-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    /*align-items: stretch;*/
    vertical-align: center;
}

.footer .footer-column {
    flex: 1 1;
}


.footer #top #showcase {
    display: flex;
    flex-direction: row;
}

.footer #top #showcase svg{
    width: 120px;
    height: auto;
    margin-right: 5px;
    align-self: center;
}

.footer #top #showcase ul{
    align-self: center;
}

.footer #emblem {
    align-self: center;
    flex: 0 1;
}

.footer #emblem img{
    height: 150px;
    margin: 0;
    align-self: center;
}

.footer #top #contact {
    display: flex;
    flex-direction: row;
    margin-top: 5px;
}

.footer #top #contact #labels li {
    margin-right: 1em;
}

.footer #top #contact .inline-label {
    display: none;
}

.footer #top #links {
    text-align: right;
}

.footer #top #links h3 {
    -webkit-margin-before: 0;
            margin-block-start: 0;
}

.footer #top #links a svg {
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
}

.footer a:link, a:visited {
    color: var(--white-1);
}

.footer a:hover {
    color: var(--accent-2-light);
}

.footer a:active {
    font-weight: bold;
}

.footer #bottom {
    padding: 10px;
    background-color: var(--accent-1);
    color: var(--white-1);
}

.footer #divider {
    height: 0.5em;
    background-color: var(--accent-2-light);
}

.footer #bottom #copyright {
    display: flex;
    align-items: center;
}

.footer #bottom #authors li {
    text-align: right;
}

.email {
    font-size: small;
}

@media (max-width: 970px) {
    .footer-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .footer #top #showcase {
        display: flex;
        flex-direction: column;
    }

    .footer #top #showcase img{
        margin: 0;
        align-self: center;
    }

    .footer #top #showcase ul{
        align-self: center;
        text-align: center;
    }

    .footer #top #contact {
        justify-content: center;
        text-align: center;
    }

    .footer #top #contact #labels  {
        display: none;
    }

    .footer #top #contact .inline-label {
        display: inline;
    }

    .footer #emblem img{
        display: none;
    }

    .footer #top #links {
        text-align: center;
    }

    .footer #top h3 {
        text-align: center;
        -webkit-margin-before: 1em;
                margin-block-start: 1em;
        -webkit-margin-after: 0.5em;
                margin-block-end: 0.5em;
    }

    .footer #top #links h3 {
        -webkit-margin-before: 1em;
                margin-block-start: 1em;
    }

    .footer #bottom #copyright {
        padding: 0;
        margin: 0;
        justify-content: center;
    }

    .footer #bottom {
        text-align: center;
    }

    .footer #bottom #authors li {
        text-align: center;
    }
}
